@import url(https://fonts.googleapis.com/css2?family=Noticia+Text:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;font-family:"Noticia Text",serif}body{padding-left:50px;padding-right:50px}.layout{display:flex;min-height:100vh}nav{margin-bottom:0px;padding-right:50px}.jumbotron{padding:0px;background-image:url(/static/media/mountain.634a762e.jpg);background-size:cover;height:100vh}.footer{padding:0px;background-image:url(/static/media/mountain.634a762e.jpg);background-size:cover;height:50}.aboutContainter{height:auto;width:100%;display:inline-block}#headshot{padding:1em;border-radius:5%;height:40%;width:40%;float:left}#aboutText{display:flex;flex-direction:column;align-content:center}@media only screen and (max-width: 960px){html{font-size:12px}.layout{flex-direction:column}}@media only screen and (max-width: 320px){html{font-size:10px}}

